/**VARIABLES**/
$color-uno:#5CD2C7;
$color-dos:#3F6460;


/**GENERAL**/ 
body {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

/** PRIMERASECCIÃ“N **/

#primeraseccion{
    background-color: $color-uno;
    h1{
        text-align: center;
    }  
    p{
        text-align: center;
    }
    div div div a {
        text-decoration: none;
        width: 100px;
        color: rgb(255, 255, 255);
        background-color: black;
        padding: 5px;
        border-radius: 6px;
        box-shadow: 6px 3px 6px rgba(0,0,0,0.3);

    }
    
}
#primeraseccion div div  div a:hover{
    background-color: green;
}



/**SERVICIOS **/

#servicios{
    background-color: $color-dos ;
    h2{
        text-align: center; 
    }
    p{
        text-align: center;
        margin-top: 1rem;
        padding: 1rem; 
    }
    div div div{
        background-color: rgb(255, 255, 255);
        border-radius: 30px;
        padding: 10px;
        box-shadow: -30px 10px 5px rgba(0,0,0,0.1);
        a{
            text-decoration: none;
            color: rgb(255, 255, 255);
            width: 150px;
            background-color: black;
            padding: 5px;
            border-radius: 6px;
            box-shadow: 6px 3px 6px rgba(0,0,0,0.3);
        }
    }

}
#servicios div div  div a:hover{
    background-color: green;
}

